import React, { useState, useEffect } from "react";
import styles from "./Exercises.module.css";
import {
  Avatar,
  IconButton,
  Button,
  Tooltip,
  Card,
  Grid,
  Checkbox,
  Dialog,
  DialogTitle,
  DialogContent,
  CircularProgress,
  Box,
  Typography,
  Radio,
} from "@material-ui/core";
import classNames from "classnames";
import FavoriteIcon from "@material-ui/icons/Favorite";
import WarningIcon from "@material-ui/icons/Warning";
import animationData from "../../../public/animation/confetti.json";
import Lottie from "react-lottie";

const Exercises = ({
  exercises,
  currentExercise,
  type,
  history,
  quizes,
  quiz,
  nextContent,
}) => {
  const [selectedAnswers, setSelectedAnswers] = useState([]);
  const [currentQuiz, setCurrentQuiz] = useState({});
  const [selectedAns, setSelectedAns] = useState([]);

  const [score, setScore] = useState({
    achieved: 0,
    total: 30,
  });

  const [successOpen, setSuccessOpen] = useState(false);
  const [warningOpen, setWarningOpen] = useState(false);

  useEffect(() => {
    if (type === "quiz") {
      if (currentExercise) {
        const element = document.getElementById(
          currentExercise.title +
            exercises.findIndex((item) => item.title === currentExercise.title)
        );
        if (element) element.scrollIntoView({ behavior: "smooth" });
      }
      let tempSelect = [];
      exercises.forEach((item) => {
        tempSelect.push({
          title: item.tile,
          answers: item.questions.map((questItem) => questItem.answer),
          userSelected: item.questions.map(() => {
            return null;
          }),
        });
      });

      setSelectedAnswers(tempSelect);
    }
  }, [currentExercise, exercises, type]);

  useEffect(() => {
    if (quiz) {
      let currQuiz = quizes.find((item) => item.slug === quiz);
      setCurrentQuiz(currQuiz);
      setScore({
        achieved: 0,
        total: currQuiz.questions.length * 10,
      });
      setSelectedAns([]);
    }
  }, [quiz]);

  // const handleSelectAnswer = (item, questionIndex) => {
  //   let temp = [...selectedAnswers];
  //   if (questionIndex !== -1) {
  //     let tempUserSelected = temp[quizIndex].userSelected;
  //     tempUserSelected[questionIndex] = item;
  //     temp[quizIndex] = {
  //       ...temp[quizIndex],
  //       userSelected: tempUserSelected,
  //     };
  //   }
  //   // setSelectedAnswers(temp);
  // };

  const handleSelectAnswer = (item, questionIndex) => {
    let newSelectedAns = [...selectedAns];
    newSelectedAns[questionIndex] = item;
    setSelectedAns(newSelectedAns);
  };

  // const onClickSubmit = (quizIndex) => {
  //   const tempSelected = selectedAnswers[quizIndex].userSelected;
  //   const correctAnswers = selectedAnswers[quizIndex].answers;
  //   let tempScore = 0;
  //   let flag = false;
  //   tempSelected.forEach((item) => {
  //     if (item) flag = true;
  //   });
  //   if (flag) {
  //     tempSelected.forEach((item, i) => {
  //       if (item) {
  //         if (item === correctAnswers[i] && item !== null) tempScore += 10;
  //       }
  //     });
  //     setScore({ ...score, achieved: tempScore });
  //     setSuccessOpen(true);
  //   } else {
  //     setWarningOpen(true);
  //   }
  // };

  const onClickSubmit = (quizId) => {
    let userSelected = [...selectedAns];
    let ans = currentQuiz.questions.map((question) => {
      return question.options.filter((option) => {
        if (option.is_correct) {
          return option;
        }
      });
    });
    if (userSelected.length > 0) {
      let tempScore = 0;
      ans.forEach((item, i) => {
        let flag = true;
        if (item.length === (userSelected[i] ? userSelected[i].length : 0)) {
          item.forEach((element) => {
            if (!userSelected[i].includes(element)) {
              flag = false;
              return;
            }
          });
        } else {
          flag = false;
        }
        if (flag) {
          tempScore = tempScore + 10;
        }
      });
      setScore({
        ...score,
        achieved: tempScore,
      });
      setSuccessOpen(true);
      setSelectedAns([]);
    } else {
      setWarningOpen(true);
    }
  };

  const handleCloseSuccess = () => {
    setSuccessOpen(false);
  };

  const handleCloseWarning = () => {
    setWarningOpen(false);
  };

  return (
    <div className={styles.container}>
      <h2>Exercises</h2>
      {type === "quiz" ? (
        <div className={styles.exercisesContainer}>
          <div className={styles.quizContainer}>
            <div className={styles.quizHeader}>
              <h5>{currentQuiz.title ? currentQuiz.title : null}</h5>
              <h5>
                Score : {score.achieved} / {score.total}
              </h5>
            </div>
            {currentQuiz.questions
              ? currentQuiz.questions.map((exercise, i) => {
                  return (
                    <ExerciseRender
                      questionIndex={i}
                      // quizIndex={quizIndex}
                      handleSelectAnswer={handleSelectAnswer}
                      exercise={exercise}
                      id={exercise.title + i}
                    />
                  );
                })
              : null}
            <div className={styles.submitBtnContainer}>
              <Button
                onClick={() => onClickSubmit(currentQuiz.id)}
                className={styles.submitBtn}
              >
                Submit
              </Button>
              <Button
                onClick={() => nextContent(quiz)}
                className={styles.submitBtn}
              >
                Next
              </Button>
            </div>
          </div>
          {/* {quizes.map((item, quizIndex) => {
            return (
              <div className={styles.quizContainer}>
                <div className={styles.quizHeader}>
                  <h5>{item.title}</h5>
                  <h5>
                    Score : {score.achieved} / {score.total}
                  </h5>
                </div>
                {item.questions.map((exercise, i) => {
                  return (
                    <ExerciseRender
                      questionIndex={i}
                      quizIndex={quizIndex}
                      handleSelectAnswer={handleSelectAnswer}
                      exercise={exercise}
                      id={exercise.title + i}
                    />
                  );
                })}
                <div className={styles.submitBtnContainer}>
                  <Button
                    onClick={() => onClickSubmit(quizIndex)}
                    className={styles.submitBtn}
                  >
                    Submit
                  </Button>
                </div>
              </div>
            );
          })} */}
          <CustomDialog
            open={successOpen}
            handleClose={handleCloseSuccess}
            score={score}
            title="Quiz Score"
            message="Hero"
            type="success"
          />

          <CustomDialog
            open={warningOpen}
            handleClose={handleCloseWarning}
            title="Solve Questions"
            message="Oops! Seems like you haven't answered any questions yet."
            type="warning"
          />
        </div>
      ) : type === "assignment" ? (
        <Assignment
          title="Assignment 1"
          src="https://anuragtech02.github.io/sample-robo/"
        />
      ) : (
        <ExercisesList quizes={quizes} history={history} />
      )}
    </div>
  );
};

export default Exercises;

const ExercisesList = ({ history, quizes }) => {
  const data = [
    {
      score: null,
      questions: 4,
      module: "ratation-and-translation",
    },
    {
      score: 30,
      questions: 4,
      module: "ratation-and-translation",
    },
    {
      score: 10,
      questions: 3,
      module: "ratation-and-translation",
    },
    {
      score: 20,
      questions: 5,
      module: "ratation-and-translation",
    },
  ];

  const handleClickQuiz = (quiz) => {
    history.push(history.location.pathname + "/q/" + quiz);
  };

  const handleClickAssignment = (assignment) => {
    history.push(
      history.location.pathname +
        "/a/" +
        assignment.toLowerCase().split(" ").join("-")
    );
  };

  return (
    <div className={styles.quizesListContainer}>
      <div className={styles.quizes}>
        <h3>Quizes</h3>
        <ul className={styles.quizesList}>
          {quizes.map((item, i) => {
            return (
              <li
                key={item.score + i}
                onClick={() => handleClickQuiz(item.slug)}
                className={styles.quiz}
              >
                <div className={styles.details}>
                  <h4>{item.title}</h4>
                  <p>{item.questions.length} Questions</p>
                </div>

                <div className={styles.progressDiv}>
                  <CircularProgressWithLabel
                    value={
                      (item.score
                        ? item.score
                        : 0 / (item.questions.length * 10)) * 100
                    }
                  />
                </div>
              </li>
            );
          })}
        </ul>
      </div>
      <div className={styles.assignments}>
        <h3>Assignments</h3>
        <ul className={styles.assignmentList}>
          {data.map((item, i) => {
            return (
              <li
                key={item.score + i}
                onClick={() =>
                  handleClickAssignment(`Assignment ${i + 1}`.toLowerCase())
                }
                className={styles.assignment}
              >
                <div className={styles.details}>
                  <h4> Quiz {i + 1}</h4>
                  <p>{item.questions} Questions</p>
                </div>

                <div className={styles.progressDiv}>
                  <CircularProgressWithLabel
                    value={(item.score / (item.questions * 10)) * 100}
                  />
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};

const ExerciseRender = ({
  exercise,
  id,
  handleSelectAnswer,
  questionIndex,
  quizIndex,
}) => {
  const [selected, setSelected] = useState([]);

  useEffect(() => {
    setSelected([]);
  }, [exercise]);

  useEffect(() => {
    // console.log(selected);
  }, [selected]);

  const handleSelectOption = (item, i) => {
    if (!selected.includes(item)) {
      setSelected([...selected, item]);
      handleSelectAnswer([...selected, item], questionIndex);
    } else {
      setSelected(selected.filter((sel) => sel !== item));
      handleSelectAnswer(
        selected.filter((sel) => sel !== item),
        questionIndex
      );
    }
  };

  return (
    <div className={styles.questionContainer} id={id}>
      <h4>{exercise.description}</h4>
      <div className={styles.optionsContainer}>
        <Grid container spacing={4}>
          {exercise.options.map((item) => {
            return (
              <Grid item md={6}>
                <div
                  className={classNames(
                    styles.option,
                    selected.includes(item) ? styles.selectedOption : ""
                  )}
                  onClick={() => handleSelectOption(item, questionIndex)}
                >
                  <Checkbox
                    checked={selected.includes(item)}
                    onChange={() => {
                      handleSelectOption(item, questionIndex);
                    }}
                    name="checkedB"
                    color="primary"
                    className={styles.checkbox}
                  />

                  {exercise.answerType === "image" ? (
                    <img
                      src={exercise.options[item]}
                      alt="option-qiuz"
                      width="150px"
                    />
                  ) : (
                    <p>{item.answer_text}</p>
                  )}
                </div>
              </Grid>
            );
          })}
        </Grid>
      </div>
    </div>
  );
};

const CustomDialog = ({ open, handleClose, score, title, message, type }) => {
  return (
    <>
      <Dialog
        onClose={handleClose}
        aria-labelledby="simple-dialog-title"
        open={open}
        style={{ zIndex: 999999 }}
      >
        <DialogTitle id="simple-dialog-title">
          <span
            className={classNames(
              styles.dialogTitle,
              type.toLowerCase() === "success"
                ? styles.successTitle
                : type.toLowerCase() === "warning"
                ? styles.warningTitle
                : ""
            )}
          >
            {title}
          </span>
        </DialogTitle>
        <DialogContent>
          <div
            className={classNames(
              styles.customDialog,
              type.toLowerCase() === "success"
                ? styles.successDialog
                : type.toLowerCase() === "warning"
                ? styles.warningDialog
                : ""
            )}
          >
            {type.toLowerCase() === "success" && score.achieved ? (
              <FavoriteIcon />
            ) : type.toLowerCase() === "warning" ? (
              <WarningIcon />
            ) : null}
            &nbsp;
            <p>
              {type.toLowerCase() === "success"
                ? score.achieved
                  ? `Congratulations! You scored : ${score.achieved} points`
                  : "Oops! Seems like you missed this time, try again."
                : type.toLowerCase() === "warning"
                ? `${message}`
                : ""}
            </p>
          </div>
        </DialogContent>
      </Dialog>
      {open && type.toLowerCase() === "success" && (
        <div
          style={{
            // zIndex: 999999999999,
            // margin: "auto",
            // width: "100%",
            // height: "100vh",
            position: "fixed",
            top: "50%",
            left: "50%",
            transform: "translate(-50%,-50%)",
          }}
        >
          <ConfettiAnim />
        </div>
      )}
    </>
  );
};

const CircularProgressWithLabel = (props) => {
  return (
    <Box
      position="relative"
      borderRadius="50%"
      boxShadow="2px 2px 10px rgba(0, 0, 0, 0.1)"
      display="inline-flex"
    >
      <CircularProgress variant="determinate" {...props} />
      <Box
        top={0}
        left={0}
        bottom={0}
        right={0}
        position="absolute"
        display="flex"
        alignItems="center"
        justifyContent="center"
      >
        <Typography
          variant="caption"
          component="div"
          color="textSecondary"
        >{`${Math.round(props.value)}%`}</Typography>
      </Box>
    </Box>
  );
};

const Assignment = ({ title, src }) => {
  return (
    <div className={styles.assignmentsContainer}>
      <h3>{title}</h3>
      <div className={styles.document}>
        <iframe
          width="100%"
          height="100%"
          frameBorder="none"
          title={title}
          src={src}
        ></iframe>
      </div>
    </div>
  );
};

const ConfettiAnim = () => {
  const defaultOptions = {
    loop: false,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  return (
    <Lottie
      options={defaultOptions}
      height={400}
      width={400}
      isStopped={false}
      isPaused={false}
    />
  );
};
